import React from "react";
import Helmet from "react-helmet";
import { Link } from "react-router-dom";
import Button from "../../Components/Button/index";
import Title from "../../Components/Title/Title";
import styled from "../../typed-components";

const Container = styled.div`
  background-color: ${props => props.theme.blueColor};
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Text = styled.div`
  color: white;
  margin-bottom: 10px;
  font-size: 14px;
`;

const ExtendedButton = styled(Button)`
  margin:0px  
  margin-top: 20px;
  background-color: ${props => props.theme.pinkColor}
  width: 250px;
  :active {
    transform: scale(0.99)
  }
`;

const CoupleVerificationPresenter: React.SFC = () => (
  <Container>
    <Helmet>
      <title>  - We There</title>
    </Helmet>
    <Title text={"Hello, There!"} />
    <Text>We There  ,</Text>
    <Text>   .</Text>
    <Text />
    <Text>   </Text>
    <Text>   </Text>
    <Text />
    <Text />
    <Link to={"/wethere-client/verify-couple/request"}>
      <ExtendedButton value={"   "} />
    </Link>
    <Link to={"/wethere-client/verify-couple/complete"}>
      <ExtendedButton value={"    "} />
    </Link>
  </Container>
);

export default CoupleVerificationPresenter;



